@import 'src/styles/helpers.scss';

.loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;

  width: 100vw;
  height: 100vh;

  transition: 1.8s $transition-timing-function 2.3s;
  background-color: color(black-alt);

  &.hide {
    transform: translateY(-100%);
    visibility: hidden;
  }
}

.row {
  padding: $col-pad-d;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    position: relative;
    z-index: 0;

    width: calc(#{$col-part-d} * 11 - #{$col-gap-d});
    height: 100%;
    display: flex;
    align-items: flex-end;
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
  }
}

.count {
  letter-spacing: -0.06em;
  @include fsl(450px, 90%);
  color: color(white);
}

.line {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;

  width: 4px;

  background-color: #303030;
}

.fill {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;

  height: 0%;
  width: 4px;

  transition: 2s $transition-timing-function;
  background: linear-gradient(90deg, #0000FF -1.21%, rgba(0, 0, 255, 0) 100%);

  &.active {
    height: 100%;
  }
}

.text {
  color: color(white);
  letter-spacing: -0.04em;
  @include fsl(80px, 90%);

  animation: blinker 1s linear infinite;
}

@keyframes blinker {  
  50% { opacity: 0; }
}